
import "./Models.css";
import TubeSubmit from "../form/TubeSubmit";
import { NavLink, useParams } from "react-router-dom";
import TubeButton from "../form/TubeChip";
import Sidebar from "./Sidebar";
import { useEffect, useState } from "react";
import { appUrl, get } from "../../utils/fetch";
import { CameraAltOutlined, VideocamOutlined, ChatOutlined, FavoriteBorderOutlined, VisibilityOutlined } from "@mui/icons-material";
import DataTablePagination from "../dataTable/DataTablePagination";
import ModelList from "../dataTable/ModelList";
import { edit as editHeader } from '../../slices/headerSlice';
import { useDispatch, useSelector } from "react-redux";

export default function Models(props){
    const { page, search } = useParams();
    const [ models, setModels ] = useState([]);
    const [ totalPages, setTotalPages ] = useState(1);

    const settings = useSelector(state => state.settings);
    const dispatch = useDispatch();

    useEffect(() => {
        get(`${appUrl}/api/models?limit=18&order=desc&column=id&page=${page}&name=${search ? search : ""}`, (result) => {
            setModels(result.data.data);
            setTotalPages(result.data.last_page);
        });
        window.scrollTo({
            top: 0
        });
        dispatch(editHeader({title: `Models - ${settings.siteTitle}`}));
    }, [page]);
    
    return (
        <div className="categories-wrapper">
            <Sidebar videos />
            <div className="categories-content">
                <div className="categories-content-header">
                    <h1>{search ? `Search result for: ${search}` : "Models"}</h1>
                </div>
                <div className="categories-content-models">
                    <ModelList data={models} />
                </div>
                <div className="categories-content-videos-pagination">
                    <DataTablePagination
                        page={page ? page : 1}
                        url={`/models${search ? `/search/${search}` : ""}`}
                        totalPages={totalPages}
                    />
                </div>
            </div>
        </div>
    );
}